.acerca{
    flex-basis:50%;
}

.infolab{
    padding:10px 30px;
}

.titlab{
    background-color:rgb(187, 187, 187);
    border-top-right-radius:20px;
    border-bottom-right-radius:20px;
    padding:5px 20px;
    color:rgb(27, 99, 121);
    margin:10px 0;
}

.estudio p{
    padding-left:40px;
    margin:10px 0;
}

progress[value]{
    width:100%;
    height:10px;
    appearance:none;
    color:rgb(115, 80, 197);
}

progress::-moz-progress-bar {background-color:rgb(115, 80, 197);}
progress::-webkit-progress-bar {background-color:rgb(115, 80, 197);}