*{
  margin: 0;
  padding: 0;
  border: 1;
  box-sizing: border-box;
}

body {
  background-color: rgb(27, 99, 121);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.contenerdor{
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}
#P{
  border: solid 1px rgb(26, 19, 92);
  width: 80%;
  margin: 0 auto;
  padding: 5px;
}
fieldset{
  margin-bottom: 1px ;        
  border: solid 1px rgb(26, 19, 92);
}
legend{
  border: solid 1px rgb(26, 19, 92);
  padding: 5px;
  font-weight: bold;
  border-radius: 20px;
  background: rgb(135, 125, 221);
}
#Foto{
  margin:5px;
  border: solid 2px rgb(26, 19, 92);
  width: 150px;
}


